.footer {
  padding-top: 40px;
  width: 100%;
  // height: 200px;
  background-color: var(--bg-secondary-color);
}
.footer__content {
  border-bottom: 1px solid var(--icon-secondary-color);
  padding-bottom: 40px;
}
.footer__block-text {
  max-width: 234px;
  width: 100%;
  // outline: 1px solid red;
  margin-bottom: 40px;
}
// .footer__contact {
//   outline: 1px solid blue;
// }

.footer__not-big-text {
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.167;
  text-transform: uppercase;

  color: var(--text-fourth-color);
  margin-top: 0;
  margin-bottom: 16px;
}

.footer__big-text {
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 1.147;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--text-third-color);
  margin-top: 0;
  margin-bottom: 0;
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background-color: var(--icon-primary-color);
  border-radius: 50%;
}
.footer__social-link:hover {
  background-color: var(--accent-color);
}

.footet__social-list {
  display: flex;
  justify-content: space-between;
  max-width: 148px;
  width: 100%;
  margin-left: auto;
  margin-bottom: 40px;
  // outline: 1px solid red;
}

.icon-github {
  width: 19px;
  height: 19px;
  fill: white;
}

.icon-facebook {
  width: 9px;
  height: 19px;
  fill: white;
}

.icon-telegram {
  width: 19px;
  height: 15px;
  fill: white;
}
.footer__text-tel {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;
  text-align: right;
  margin-bottom: 0;
}

.footer__down-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;

  text-align: center;

  color: #907e82;
  width: 150px;
  margin-bottom: 8px;

  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;
}
// media

@media screen and (min-width: 768px) {
  .footer__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .footer__block-text {
    max-width: 340px;
    width: 100%;

    margin-bottom: 0;
  }

  .footer__not-big-text {
    font-size: 26px;
    line-height: 1.154;
    letter-spacing: 0.04em;

    margin-bottom: 12px;
  }

  .footer__big-text {
    font-size: 48px;
    line-height: 1.146;
    letter-spacing: 0.06em;
  }

  .footer__social-link {
    width: 34px;
    height: 34px;
  }
  .icon-github {
    width: 20px;
    height: 20px;
  }

  .icon-facebook {
    width: 9px;
    height: 20px;
  }

  .icon-telegram {
    width: 20px;
    height: 16px;
  }

  .footer__text-tel {
    font-size: 14px;
    padding-bottom: 19px;
    margin-bottom: 0;
  }

  .footer__down-text {
    width: 278px;
    line-height: 1.86;
    letter-spacing: 0.02em;
    margin-top: 8px;
  }
}

@media screen and (min-width: 1200px) {
  .footer {
    padding-top: 120px;
  }

  .footer__content {
    padding-bottom: 60px;
  }

  .footer__block-text {
    max-width: 480px;
  }

  .footer__not-big-text {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 16px;
  }

  .footer__big-text {
    font-size: 58px;
    line-height: 1.138;
  }

  .footer__text-tel {
    font-size: 16px;
    padding-bottom: 0;
    letter-spacing: 0.02em;
  }
}
