h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-primary-color);
  background-color: var(--bg-primary-color);
  font-family: var(--primary-font);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.overflow-hidden {
  overflow: hidden;
}

:root {
  --text-primary-color: #000;
  --text-secondary-color: #fff;
  --text-third-color: #d41443;
  --text-fourth-color: #ffa5ba;
  --text-fifth-color: #907e82;
  --bg-modal-color: #e17992;
  --menu-border-color: #e18298;
  --icon-primary-color: #fea5bb;
  --icon-secondary-color: #e1e1e1;
  --icon-third-color: #d31342;
  --icon-fourth-color: #c7133e;
  --accent-color: #d41443;
  --btn-hover-bg-color: #ecebeb;
  --btn-red-hover-bg-color: #ffb8ca;
  --bg-backdrop-color: rgba(0, 0, 0, .2);
  --bg-primary-color: #fff;
  --bg-secondary-color: #fff5f6;
  --bg-third-color: #ffb8ca;
  --bg-fourth-color: #f0d1a5;
  --bg-fifth-color: #c2e297;
  --bg-six-color: #fafafa;
  --primary-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", cursive;
  --transition-duration-function: 250ms cubic-bezier(.4, 0, .2, 1);
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 16px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  transition: color var(--transition-duration-function);
  font-style: normal;
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--accent-color);
}

.button {
  min-width: 160px;
  text-align: center;
  cursor: pointer;
  color: var(--accent-color);
  background-color: var(--bg-primary-color);
  transition: background-color var(--transition-duration-function);
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 0 20px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 44px;
  display: inline-flex;
}

@media (min-width: 768px) {
  .button {
    gap: 12px;
    padding: 0 30px;
  }
}

@media (min-width: 1200px) {
  .button {
    gap: 14px;
    padding: 0 35px;
  }
}

.button__icon {
  fill: var(--accent-color);
}

.button:hover, .button:focus {
  color: var(--accent-color);
  background: var(--btn-hover-bg-color);
}

.button--red {
  color: var(--text-secondary-color);
  background-color: var(--accent-color);
}

.button--red .button__icon {
  fill: var(--text-secondary-color);
}

.button--red:hover, .button--red:focus {
  background: var(--btn-hover-bg-color);
  color: var(--accent-color);
}

.button--red:hover .button__icon, .button--red:focus .button__icon {
  fill: var(--accent-color);
}

.btn-small {
  min-width: 63px;
  color: var(--bg-primary-color);
  background-color: var(--accent-color);
  cursor: pointer;
  border: none;
  border-radius: 20px;
  padding: 7px 14px;
  font-family: inherit;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  display: inline-block;
}

.btn-small:hover, .btn-small:focus {
  background: var(--btn-hover-bg-color);
  color: var(--accent-color);
}

@media (min-width: 1200px) {
  .btn-small {
    min-width: 107px;
    padding: 11px 21px;
    font-size: 14px;
    line-height: 1.3;
  }
}

.btn-small--white {
  color: var(--accent-color);
  background-color: var(--bg-primary-color);
}

.spacer {
  height: 0;
}

.header {
  background-color: var(--bg-third-color);
  padding: 6px 0 1px;
  position: relative;
}

@media (min-width: 768px) {
  .header {
    padding: 30px 0 1px;
  }
}

@media (min-width: 1200px) {
  .header {
    padding: 35px 0 1px;
  }
}

.header__scrolled {
  z-index: 10;
  padding: 1px 0;
  animation-name: fadein;
  animation-duration: .3s;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.header__scrolled ~ .spacer {
  background-color: var(--bg-third-color);
  height: 5px;
}

@media (min-width: 768px) {
  .header__scrolled ~ .spacer {
    height: 29px;
  }
}

@media (min-width: 1200px) {
  .header__scrolled ~ .spacer {
    height: 34px;
  }
}

.header__inner {
  height: 52px;
  flex-basis: 100%;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
  display: flex;
  overflow: auto;
}

@media (min-width: 1200px) {
  .header__inner {
    gap: 20px;
  }
}

.header__logo {
  display: block;
}

.header__nav {
  display: none;
}

@media (min-width: 1200px) {
  .header__nav {
    margin: 0 auto;
    display: flex;
  }
}

.header__nav-list {
  display: flex;
}

.header__nav-item {
  padding: 13px 18px;
}

.header__nav-link {
  color: var(--text-secondary-color);
  font-size: 16px;
  font-weight: 700;
}

.header__button {
  display: none;
}

@media (min-width: 768px) {
  .header__button {
    display: flex;
  }
}

.header__button-open {
  cursor: pointer;
  background: none;
  border: none;
  align-items: center;
  margin-left: auto;
  padding: 4px;
  display: flex;
}

@media (min-width: 1200px) {
  .header__button-open {
    display: none;
  }
}

.header__mobile-menu {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: var(--bg-backdrop-color);
  transition: opacity .15s linear, visibility .25s linear;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

@media (min-width: 1200px) {
  .header__mobile-menu {
    display: none;
  }
}

.header__mobile-menu.is-open {
  opacity: 1;
  pointer-events: initial;
  visibility: visible;
}

.header__button-close {
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 4px;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

@media (min-width: 768px) {
  .header__button-close {
    right: 32px;
  }
}

.header__menu-content {
  width: 250px;
  height: 100%;
  background-color: var(--bg-modal-color);
  background-color: var(--bg-modal-color);
  flex-direction: column;
  gap: 19px;
  padding: 60px 0;
  transition: transform .25s linear;
  display: flex;
  position: relative;
  overflow: auto;
  transform: translateX(-100%);
  box-shadow: 8px 0 20px rgba(0, 0, 0, .25);
}

.header__mobile-menu.is-open .header__menu-content {
  transform: translateX(0);
}

@media (min-width: 768px) {
  .header__menu-content {
    width: 254px;
  }
}

.header__mobile-nav-list {
  flex-direction: column;
  display: flex;
}

.header__mobile-nav-item {
  border-top: 1px solid var(--menu-border-color);
  border-bottom: 1px solid var(--menu-border-color);
  display: block;
}

.header__mobile-nav-link {
  color: var(--text-secondary-color);
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.32;
  display: block;
}

.header__mobile-button {
  order: -1;
  margin: 0 20px;
}

@media (min-width: 768px) {
  .header__mobile-button {
    margin: 0 34px;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
    transform: translateY(-52px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

